/* Section */

.section {
    margin-top: 20px;
    padding: 5px 10%;
}

.section .textoMarca {
    text-align: center;
    font-size: 30pt;
    font-weight: bold;
    margin-bottom: 20px;
}

.section .cards {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
}

.section .cards .cards-item {
    display: flex;
    padding: .5rem;
    text-decoration: none;
}

.section .cards .cards-item a{
    text-decoration: none;
}

.section .cards .cards-item .card img {
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    border-radius: .75rem;
}

.section .cards .cards-item .card .descrip {
    padding: 9px 5px;
    margin: 5px 0;
    font-size: 15pt;
    font-weight: 600;

}

.section .cards .cards-item .card .price {
    background-color: #263238;
    color: #fafafa;
    max-width: fit-content;
    border-radius: 10rem;
    font-size: medium;
    font-weight: bolder;
    padding: 5px 13px;
    margin: 0px 0;
}

@media (min-width: 1rem) {
    .cards-item {
        width: 50%;
    }
    .section .cards .cards-item .card .descrip {
        font-size: medium;
    }

}

@media (min-width: 56rem) {
    .cards-item {
        width: 33.3333%;
    }
}

@media screen and (max-width: 800px) {

    /* Section */
    .section {
        padding: 5px 1%;
    }
}